	module.exports = {"data":{"markdownRemark":{"html":"<p>You can award a hacker with various swag in addition to or instead of bounties. Limited edition swag is often well-received as well as free coupons or vouchers for the services or products your organization offers. You can also offer to host hackers near your office or cover their admission to conferences your team may be attending.</p>\n<p>To award a hacker with swag:</p>\n<ol>\n<li>Go to <strong>Settings > Program > Rewards > Swag</strong>.</li>\n<li>Click <strong>Turn on</strong> to enable your program to reward hackers with swag.</li>\n<li>Go to your inbox and open the report you'd like to award swag for.</li>\n<li>Expand the action picker at the bottom of the report above the comment box.</li>\n<li>Select <strong>Set award</strong>.</li>\n<li>In the next action picker, change <strong>Award</strong> to <strong>Swag</strong> in the drop-down list.</li>\n</ol>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/swag-4bc446a382bdc49b9c98c06867c82608-e754f.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 32.64781491002571%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"swag\"\n        title=\"\"\n        src=\"/static/swag-4bc446a382bdc49b9c98c06867c82608-2f183.png\"\n        srcset=\"/static/swag-4bc446a382bdc49b9c98c06867c82608-67829.png 125w,\n/static/swag-4bc446a382bdc49b9c98c06867c82608-5c59f.png 250w,\n/static/swag-4bc446a382bdc49b9c98c06867c82608-2f183.png 500w,\n/static/swag-4bc446a382bdc49b9c98c06867c82608-c93e8.png 750w,\n/static/swag-4bc446a382bdc49b9c98c06867c82608-e754f.png 778w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<ol start=\"7\">\n<li><em>(Optional)</em> Add a comment about the award in the comment box.</li>\n<li>Click <strong>Set award</strong>.</li>\n<li>Go back to <strong>Settings > Program > Rewards > Swag</strong>.</li>\n</ol>\n<p>You'll see an overview of swag requests that are sent and unsent as well as hacker mailing addresses. Hackers that haven't provided a mailing address or a T-shirt size will be asked to update their profile to provide the necessary information for them to receive swag.</p>\n<p>You can select and de-select the checkbox to manage which requests have been sent and unsent.</p>\n<p>Your program is responsible for fulfillment and delivery of swag to the hacker.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/swag-1-3320e982d7e3289ddf524cf01462bbe6-b8d94.png\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 500px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 54.46153846153846%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"swag\"\n        title=\"\"\n        src=\"/static/swag-1-3320e982d7e3289ddf524cf01462bbe6-2f183.png\"\n        srcset=\"/static/swag-1-3320e982d7e3289ddf524cf01462bbe6-67829.png 125w,\n/static/swag-1-3320e982d7e3289ddf524cf01462bbe6-5c59f.png 250w,\n/static/swag-1-3320e982d7e3289ddf524cf01462bbe6-2f183.png 500w,\n/static/swag-1-3320e982d7e3289ddf524cf01462bbe6-c93e8.png 750w,\n/static/swag-1-3320e982d7e3289ddf524cf01462bbe6-4e628.png 1000w,\n/static/swag-1-3320e982d7e3289ddf524cf01462bbe6-b8d94.png 1300w\"\n        sizes=\"(max-width: 500px) 100vw, 500px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>","frontmatter":{"path":"/programs/swag.html","id":"programs/swag","title":"Swag","description":null}}},"pathContext":{}}

